@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(rgba(4,9,30,0.4), rgba(4,9,30,0.8)),url(../img/fam.jpg);
	background-position: center;
  background-repeat: no-repeat;
	background-size: cover;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.box {
  display: block;
  margin: 20px auto;
  max-width: 500px;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 10px;
}

form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
}

form input {
  width: 70%;
  border-radius: 4px;
  border: 1px solid #ddd;
  padding: 10px 20px;
  background-color: ivory;
}

form button {
  width: auto;
  max-width: 20%;
  background-color: salmon;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  cursor: pointer;
}

form button:hover {
  background-color: white;
  color: salmon;
  border: 1px solid salmon;
}

h1 {
  color: white;
  font-family: 'Press Start 2P';
}

img {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
  border-radius: 6px;
}

.image-container {
  padding: 5px;
  border-radius: 6px;
  border: 1px solid #dddd;
}

button {
  background-color: salmon;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  cursor: pointer;
  display: block;
  margin: 20px auto;
}

button:hover {
  background-color: white;
  color: salmon;
  border: 1px solid salmon;
}